import React, { useState } from 'react';
import { Modal } from '@consta/uikit/Modal';
import { Text } from '@consta/uikit/Text';

export default function RibbonMouthsCoords(): JSX.Element {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <>
      <section onClick={() => setIsOpen(true)} className="button" />

      <Modal
        isOpen={isOpen}
        hasOverlay
        onOverlayClick={(): void => setIsOpen(false)}
      >
        <Text>Загрузка устьев координат</Text>
      </Modal>
    </>
  );
}
